import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Collapse from "@mui/material/Collapse";

const dispatchedEvents = [];

const SrsMuiCollapse = function (props, ref) {
  return (
    <Collapse {...props} theme={props.muiTheme}>
      <slot />
    </Collapse>
  );
};

const styles = `
  /* Custom styles for the MuiCollapse component */
`;

const props = [
  {
    name: "in",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "collapsedHeight",
    defaultValue: "0px",
    type: "string",
  },
  {
    name: "timeout",
    defaultValue: "auto",
    type: "string",
  },
  {
    name: "orientation",
    defaultValue: "vertical",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Collapse if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCollapse),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
